@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600;700&display=swap');

:root {
  --ink: #0f172a;
  --muted: #64748b;
  --border: #e2e8f0;
  --card: #ffffff;
  --primary: #2563eb;
  --primary-strong: #1d4ed8;
  --accent: #a855f7;
  --surface: #f8fafc;
  --danger: #dc2626;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: 'Inter', sans-serif;
  background: radial-gradient(circle at 20% 20%, #eef2ff 0, transparent 30%),
    radial-gradient(circle at 80% 10%, #fef9c3 0, transparent 25%),
    linear-gradient(180deg, #f8fafc 0%, #e2e8f0 100%);
  color: var(--ink);
}

a {
  color: var(--primary);
  text-decoration: none;
}

.auth-shell {
  min-height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 32px;
}

.auth-card {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 32px;
  width: 100%;
  max-width: 440px;
  box-shadow: 0 25px 80px rgba(15, 23, 42, 0.1);
}

.auth-title {
  margin: 0 0 12px;
}

.auth-footer {
  margin-top: 12px;
  color: var(--muted);
}

.form {
  display: flex;
  flex-direction: column;
  gap: 12px;
}

.form-label {
  font-size: 14px;
  color: var(--muted);
}

.form-input,
.form-group input,
.form-group select {
  width: 100%;
  padding: 10px 12px;
  border-radius: 10px;
  border: 1px solid var(--border);
  background: #f8fafc;
  font-size: 14px;
  outline: none;
}

.form-input:focus,
.form-group input:focus,
.form-group select:focus {
  border-color: var(--primary);
  box-shadow: 0 0 0 3px rgba(37, 99, 235, 0.15);
}

.primary-btn {
  padding: 12px 14px;
  background: linear-gradient(120deg, var(--primary) 0%, var(--accent) 100%);
  color: #fff;
  border: none;
  border-radius: 12px;
  font-weight: 700;
  cursor: pointer;
  transition: transform 120ms ease, box-shadow 120ms ease;
}

.primary-btn:hover {
  transform: translateY(-1px);
  box-shadow: 0 12px 30px rgba(37, 99, 235, 0.25);
}

.ghost-btn {
  padding: 10px 12px;
  border: 1px solid var(--border);
  background: transparent;
  color: var(--ink);
  border-radius: 10px;
  cursor: pointer;
}

.danger-btn {
  padding: 8px 10px;
  border: 1px solid transparent;
  background: var(--danger);
  color: #fff;
  border-radius: 10px;
  cursor: pointer;
}

.form-error {
  color: var(--danger);
  margin: 4px 0 0;
  font-size: 14px;
}

.form-success {
  color: #0f766e;
  margin: 4px 0 0;
  font-size: 14px;
}

.dashboard-shell {
  display: flex;
  min-height: 100vh;
  background: var(--surface);
}

.sidebar {
  width: 240px;
  background: #0b1224;
  color: #e2e8f0;
  padding: 24px;
  display: flex;
  flex-direction: column;
  gap: 24px;
}

.sidebar-brand {
  display: flex;
  align-items: center;
  gap: 12px;
}

.brand-dot {
  width: 40px;
  height: 40px;
  border-radius: 12px;
  background: linear-gradient(180deg, var(--primary) 0%, var(--accent) 100%);
}

.sidebar-nav {
  display: flex;
  flex-direction: column;
  gap: 8px;
}

.nav-link {
  color: #e2e8f0;
  padding: 12px 14px;
  border-radius: 10px;
  text-decoration: none;
  border: 1px solid transparent;
}

.nav-link:hover {
  background: rgba(226, 232, 240, 0.08);
}

.nav-link-active {
  background: rgba(37, 99, 235, 0.15);
  border-color: rgba(37, 99, 235, 0.4);
}

.content {
  flex: 1;
  padding: 28px;
  overflow-y: auto;
}

.content-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 18px;
}

.user-chip {
  display: flex;
  align-items: center;
  gap: 10px;
}

.chip-name {
  font-weight: 600;
}

.eyebrow {
  text-transform: uppercase;
  letter-spacing: 0.08em;
  font-size: 12px;
  color: var(--muted);
  margin: 0 0 4px;
}

.panel {
  background: var(--card);
  border: 1px solid var(--border);
  border-radius: 16px;
  padding: 20px;
  margin-bottom: 24px;
  box-shadow: 0 18px 40px rgba(15, 23, 42, 0.06);
}

.panel-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 14px;
}

.badge {
  display: inline-block;
  padding: 6px 10px;
  background: rgba(37, 99, 235, 0.12);
  color: var(--primary);
  border-radius: 999px;
  font-weight: 600;
  font-size: 12px;
}

.grid-form {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 16px;
}

.form-group {
  display: flex;
  flex-direction: column;
  gap: 6px;
  font-size: 14px;
}

.form-actions {
  display: flex;
  align-items: center;
  gap: 12px;
}

.table-shell {
  margin-top: 12px;
}

.data-table {
  width: 100%;
  border-collapse: collapse;
  background: var(--card);
}

.data-table th,
.data-table td {
  padding: 12px;
  border-bottom: 1px solid var(--border);
  text-align: left;
}

.data-table th {
  font-size: 13px;
  color: var(--muted);
  text-transform: uppercase;
  letter-spacing: 0.04em;
}

.table-actions {
  display: flex;
  gap: 8px;
}

.pill {
  display: inline-flex;
  align-items: center;
  padding: 6px 10px;
  border-radius: 999px;
  font-weight: 600;
  font-size: 12px;
}

.pill-green {
  background: rgba(16, 185, 129, 0.15);
  color: #0f766e;
}

.pill-amber {
  background: rgba(245, 158, 11, 0.15);
  color: #92400e;
}

.muted {
  color: var(--muted);
}

.info-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  gap: 14px;
}

.info-card {
  border: 1px dashed var(--border);
  border-radius: 14px;
  padding: 14px;
  background: #f8fafc;
}

.map-frame {
  margin-top: 18px;
  border-radius: 16px;
  overflow: hidden;
  border: 1px solid var(--border);
}

.map-frame iframe {
  width: 100%;
  height: 320px;
  border: 0;
}

.edit-panel {
  margin-top: 16px;
}

.table-title {
  margin: 0 0 8px;
}
